import axios from "axios";
import * as moment from "moment";
import history from "../../history";
import * as actions from "../api";

const api = ({ dispatch }) => (next) => async (action) => {
  if (action.type !== actions.apiCallBegan.type) return next(action);

  const { url, method, data, onStart, onSuccess, onError } = action.payload;

  if (onStart) dispatch({ type: onStart });

  next(action);

  try {
    const response = await axios.request({
      baseURL: "https://api.openweathermap.org",
      url,
      method,
      data,
    });

    const structuredData = await [
      ...response.data.list.map((x) => {
        return {
          tempC: Number(x.main.temp).toFixed(),
          tempCMax: Number(x.main.temp_max).toFixed(),
          tempCMin: Number(x.main.temp_min).toFixed(),
          tempF: Number(x.main.temp * 1.8 + 32).toFixed(),
          tempFMax: Number(x.main.temp_max * 1.8 + 32).toFixed(),
          tempFMin: Number(x.main.temp_min * 1.8 + 32).toFixed(),
          humidity: x.main.humidity,
          date: moment(x.dt_txt).format("DD MMM YY"),
          dt_txt: x.dt_txt,
          dt: x.dt,
          description: x.weather[0].description,
          icon: x.weather[0].icon,
          wind: Number((x.wind.speed * (60 * 60)) / 1000).toFixed(),
        };
      }),
    ];

    if (onSuccess) {
      dispatch({
        type: onSuccess,
        payload: { forecast: structuredData, location: response.data.city },
      });
      setTimeout(() => {
        history.push("/weather");
      }, 500);
    }
  } catch (error) {
    if (onError) dispatch({ type: onError, payload: error.message });
  }
};

export default api;
