import { createSlice } from "@reduxjs/toolkit";
import { createSelector } from "reselect";
import { apiCallBegan } from "./api";
import moment from "moment";

const slice = createSlice({
  name: "forecast",
  initialState: {
    list: [],
    location: {},
    barchart: [],
    selected: [],
    units: "imperial",
    loading: false,
    error: null,
  },
  reducers: {
    forcastRequested: (forcasts) => {
      forcasts.loading = true;
    },

    forcastReceived: (forcasts, action) => {
      forcasts.list = action.payload.forecast;
      forcasts.location = action.payload.location;
      forcasts.barchart = [];
      forcasts.loading = false;
    },

    forcastRequestFailed: (forcasts) => {
      forcasts.loading = false;
    },

    updateUnits: (forcasts, action) => {
      forcasts.units = action.payload;
    },

    generateDayForcast: (forcasts, action) => {
      forcasts.barchart = action.payload.datas;
    },

    removeForcastsPerDay: (forcasts) => {
      forcasts.barchart = [];
      forcasts.selected = [];
    },

    selectDayForcast: (forcasts, action) => {
      forcasts.selected = action.payload;
    },

    setError: (forcasts, action) => {
      forcasts.error = action.payload;
    },
  },
});

export const {
  forcastReceived,
  forcastRequested,
  forcastRequestFailed,
  generateDayForcast,
  removeForcastsPerDay,
  updateUnits,
  selectDayForcast,
  setError,
} = slice.actions;

export default slice.reducer;

// Action Creators
export const loadForcasts = (position) => (dispatch, getState) => {
  const { latitude, longitude } = position.coords;
  const url = `/data/2.5/forecast?lat=${latitude}&lon=${longitude}&APPID=${process.env.REACT_APP_WEATHER_API_KEY}&cnt=40&units=metric`;

  return dispatch(
    apiCallBegan({
      url,
      onStart: forcastRequested.type,
      onSuccess: forcastReceived.type,
      onError: forcastRequestFailed.type,
    })
  );
};

export const getGroupedForcasts = createSelector(
  (state) => state.forecasts,
  (forecasts) =>
    forecasts.list.reduce((accumulator, currentValue) => {
      const date = moment(currentValue.dt_txt).format("DD");

      accumulator[date] = [...(accumulator[date] || []), currentValue];
      return accumulator;
    }, {})
);

export const generateForcastsPerDay = () => (dispatch, getState) => {
  const { units, selected } = getState().forecasts;
  const m = units === "metric" ? "C" : "F";

  if (!selected) {
    return;
  }

  const datas = [
    ...selected.map((daily) => {
      return {
        name: m === "C" ? `${daily.tempC} ${m}` : `${daily.tempF} ${m}`,
        temp: m === "C" ? daily.tempC : daily.tempF,
      };
    }),
  ];

  return dispatch(
    generateDayForcast({
      datas,
    })
  );
};
