import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Avatar from "@material-ui/core/Avatar";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import WindIcon from "./wind.svg";
import HumidityIcon from "./humidity.svg";
import ThermometerIcon from "./thermometer.svg";

const useStyles = makeStyles((theme) => ({
  pad: {
    padding: "10px",
    boxSizing: "border-box",
  },
  selected: {
    color: theme.palette.primary.contrastText,
    backgroundColor: theme.palette.primary.dark,
  },
  title: {
    fontSize: 14,
  },
  temp: {
    marginTop: 4,
    marginBottom: 12,
    color: "white",
  },
  info: {
    textTransform: "capitalize",
    marginBottom: 30,
    fontSize: 13,
    color: "white",
  },
  micon: {
    width: 64,
    height: 64,
  },
  icon: {
    width: 24,
    height: 24,
    marginRight: 6,
  },
  content: {
    "&:last-child": {
      paddingBottom: 16,
    },
  },
  footer: {
    fontSize: 12,
    color: "white",
  },
  card: {
    backgroundColor: theme.palette.primary.light,
  },
  date: {
    color: "white",
  },
}));

const ForcastsListItem = ({
  forecast,
  index,
  sliderChange,
  metric,
  selected,
}) => {
  const classes = useStyles();

  const {
    tempC,
    tempCMax,
    tempCMin,
    tempF,
    tempFMax,
    tempFMin,
    humidity,
    date,
    description,
    icon,
    wind,
  } = forecast;

  return (
    <div className={classes.pad}>
      <Card
        className={`${index === selected ? classes.selected : classes.card}`}
        onClick={() => sliderChange(index)}
      >
        <CardContent className={classes.content}>
          <Grid container spacing={3}>
            <Grid item xs={8} sm container>
              <Grid item xs={12}>
                <Typography className={classes.date}>{date}</Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography
                  className={classes.temp}
                  variant="h5"
                  component="h2"
                >
                  {metric === "imperial" ? `${tempF} °F` : `${tempC} °C`}
                </Typography>
              </Grid>

              <Typography className={classes.info} color="textSecondary">
                {description}
              </Typography>
            </Grid>
            <Grid item>
              <Avatar
                alt="Remy Sharp"
                className={classes.micon}
                src={`http://openweathermap.org/img/wn/${icon}@2x.png`}
              />
            </Grid>
          </Grid>
          <Grid item sm container>
            <Grid item xs={4}>
              <Box display="flex" flexDirection="row" alignItems="center">
                <Avatar
                  alt="Remy Sharp"
                  className={classes.icon}
                  src={ThermometerIcon}
                />
                <Typography color="textSecondary" className={classes.footer}>
                  {metric === "imperial"
                    ? `${tempFMax} / ${tempFMin}`
                    : `${tempCMax} / ${tempCMin}`}
                </Typography>
              </Box>
            </Grid>

            <Grid item xs={4} className={classes.footer}>
              <Box
                display="flex"
                flexDirection="row"
                alignItems="center"
                justifyContent="center"
              >
                <Avatar
                  alt="Remy Sharp"
                  className={classes.icon}
                  src={WindIcon}
                />
                <Typography color="textSecondary" className={classes.footer}>
                  {wind} km/h
                </Typography>
              </Box>
            </Grid>
            <Grid item xs={4} className={classes.footer}>
              <Box
                display="flex"
                flexDirection="row"
                alignItems="center"
                justifyContent="center"
              >
                <Avatar
                  alt="Remy Sharp"
                  className={classes.icon}
                  src={HumidityIcon}
                />
                <Typography className={classes.footer}>{humidity}</Typography>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </div>
  );
};

export default ForcastsListItem;
